import React from 'react';
import { FormControl, FormControlProps } from '@mui/material';

interface CustomFormControlProps extends FormControlProps {
  size?: 'small' | 'medium';
}

/**
 * 
 * @param size - small, medium
 * @param sx - 
 * @param otherProps - FormControlProps
 * @returns FormControl
 */
const CustomFormControl: React.FC<CustomFormControlProps> = ({
  size = 'medium',
  sx,
  ...otherProps
}) => {
  // 
  const getFontSize = () => {
    switch (size) {
      case 'small':
        return '0.85rem';
      default: // medium
        return '1rem';
    }
  };

  return (
    <FormControl
      size={size}
      fullWidth
      sx={{
        background: '#fff',
        borderRadius: 2,
        fontSize: getFontSize(),
        ...sx,
      }}
      {...otherProps}
    />
  );
};

export default CustomFormControl; 