import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Task, TaskState, Subtask } from '../types/task';

const defaultTasks: Task[] = [
  {
    id: '1',
    title: 'TaskManager',
    description: '',
    categoryId: '3', // 
    priority: 'medium',
    completed: false,
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString(),
    subtasks: [],
  },
  {
    id: '2',
    title: '',
    description: 'Recharts',
    categoryId: '3',
    priority: 'high',
    completed: true,
    createdAt: new Date(Date.now() - 2 * 24 * 60 * 60 * 1000).toISOString(), // 2
    updatedAt: new Date(Date.now() - 1 * 24 * 60 * 60 * 1000).toISOString(), // 1
    subtasks: [],
  },
  {
    id: '3',
    title: 'UI',
    description: 'Material-UI',
    categoryId: '3',
    priority: 'medium',
    completed: false,
    createdAt: new Date(Date.now() - 1 * 24 * 60 * 60 * 1000).toISOString(), // 1
    updatedAt: new Date(Date.now() - 1 * 24 * 60 * 60 * 1000).toISOString(),
    subtasks: [],
  },
  {
    id: '4',
    title: '',
    description: '',
    categoryId: '3',
    priority: 'low',
    completed: true,
    createdAt: new Date(Date.now() - 3 * 24 * 60 * 60 * 1000).toISOString(), // 3
    updatedAt: new Date(Date.now() - 2 * 24 * 60 * 60 * 1000).toISOString(), // 2
    subtasks: [],
  },
];

const initialState: TaskState = {
  tasks: defaultTasks,
  loading: false,
  error: null,
};

const taskSlice = createSlice({
  name: 'tasks',
  initialState,
  reducers: {
    addTask: (state, action: PayloadAction<Omit<Task, 'id' | 'createdAt' | 'updatedAt'>>) => {
      const newTask: Task = {
        ...action.payload,
        priority: action.payload.priority || 'medium',
        id: Date.now().toString(),
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString(),
        subtasks: [],
      };
      state.tasks.push(newTask);
    },
    updateTask: (state, action: PayloadAction<{ id: string; updates: Partial<Omit<Task, 'id' | 'createdAt'>> }>) => {
      const { id, updates } = action.payload;
      const taskIndex = state.tasks.findIndex(task => task.id === id);
      if (taskIndex !== -1) {
        state.tasks[taskIndex] = {
          ...state.tasks[taskIndex],
          ...updates,
          updatedAt: new Date().toISOString(),
        };
      }
    },
    deleteTask: (state, action: PayloadAction<string>) => {
      state.tasks = state.tasks.filter(task => task.id !== action.payload);
    },
    toggleTaskComplete: (state, action: PayloadAction<string>) => {
      const taskIndex = state.tasks.findIndex(task => task.id === action.payload);
      if (taskIndex !== -1) {
        state.tasks[taskIndex].completed = !state.tasks[taskIndex].completed;
        state.tasks[taskIndex].updatedAt = new Date().toISOString();
      }
    },
    // 
    addSubtask: (state, action: PayloadAction<{ taskId: string; title: string }>) => {
      const { taskId, title } = action.payload;
      const task = state.tasks.find(t => t.id === taskId);
      if (task) {
        const newSubtask: Subtask = {
          id: Date.now().toString(),
          title,
          completed: false,
        };
        if (!task.subtasks) task.subtasks = [];
        task.subtasks.push(newSubtask);
        task.updatedAt = new Date().toISOString();
      }
    },
    // 
    deleteSubtask: (state, action: PayloadAction<{ taskId: string; subtaskId: string }>) => {
      const { taskId, subtaskId } = action.payload;
      const task = state.tasks.find(t => t.id === taskId);
      if (task && task.subtasks) {
        task.subtasks = task.subtasks.filter(st => st.id !== subtaskId);
        task.updatedAt = new Date().toISOString();
      }
    },
    // 
    toggleSubtaskComplete: (state, action: PayloadAction<{ taskId: string; subtaskId: string }>) => {
      const { taskId, subtaskId } = action.payload;
      const task = state.tasks.find(t => t.id === taskId);
      if (task && task.subtasks) {
        const subtask = task.subtasks.find(st => st.id === subtaskId);
        if (subtask) {
          subtask.completed = !subtask.completed;
          task.updatedAt = new Date().toISOString();
        }
      }
    },
  },
});

export const { addTask, updateTask, deleteTask, toggleTaskComplete, addSubtask, deleteSubtask, toggleSubtaskComplete } = taskSlice.actions;
export default taskSlice.reducer; 