import React from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Box,
} from '@mui/material';
import CustomButton from '../custom_props/CustomButton';
import CustomTypography from '../custom_props/CustomTypography';
import { COMMON_STYLES } from '../custom_props/styles';

interface ColorWarningDialogProps {
  open: boolean;
  onClose: () => void;
  onConfirm: () => void;
  newColor: string;
  similarColors: string[];
}

/**
 * 
 * 
 * @param open - 
 * @param onClose - 
 * @param onConfirm - 
 * @param newColor - 
 * @param similarColors - 
 */
const ColorWarningDialog: React.FC<ColorWarningDialogProps> = ({
  open,
  onClose,
  onConfirm,
  newColor,
  similarColors,
}) => {
  return (
    <Dialog open={open} onClose={onClose} maxWidth="sm" fullWidth>
      <DialogTitle sx={COMMON_STYLES.dialog.title}>
        
      </DialogTitle>
      
      <DialogContent sx={{ p: 4, ...COMMON_STYLES.dialog.content }}>
        <CustomTypography variant="body1" sx={{ mb: 2 }}>
          
        </CustomTypography>
        
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 2 }}>
          <CustomTypography variant="body2">:</CustomTypography>
          <Box
            sx={{
              width: 24,
              height: 24,
              backgroundColor: newColor,
              borderRadius: '50%',
              border: '1px solid',
              borderColor: 'divider',
            }}
          />
        </Box>
        
        {similarColors.length > 0 && (
          <Box>
            <CustomTypography variant="body2" sx={{ mb: 1 }}>
              :
            </CustomTypography>
            <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap' }}>
              {similarColors.map((color, index) => (
                <Box
                  key={index}
                  sx={{
                    width: 20,
                    height: 20,
                    backgroundColor: color,
                    borderRadius: '50%',
                    border: '1px solid',
                    borderColor: 'divider',
                  }}
                />
              ))}
            </Box>
          </Box>
        )}
      </DialogContent>
      
      <DialogActions sx={{ px: 4, py: 2, ...COMMON_STYLES.dialog.actions }}>
        <CustomButton 
          onClick={onClose}
          variant="outlined"
          color="primary"
          size="medium"
        >
          
        </CustomButton>
        <CustomButton 
          onClick={onConfirm} 
          variant="contained"
          color="primary"
          size="medium"
        >
          
        </CustomButton>
      </DialogActions>
    </Dialog>
  );
};

export default ColorWarningDialog; 