import React, { useState } from 'react';
import {
  Box,
  InputLabel,
  Select,
  MenuItem,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
} from '@mui/material';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import { Cancel, Add } from '@mui/icons-material';
import { useAppDispatch, useAppSelector } from '../hooks/redux';
import { addTask } from '../store/taskSlice';
import { Priority, Subtask } from '../types/task';
import CustomButton from '../custom_props/CustomButton';
import CustomTextField from '../custom_props/CustomTextField';
import CustomFormControl from '../custom_props/CustomFormControl';

import SubtaskManager from './SubtaskManager';
import { COMMON_STYLES } from '../custom_props/styles';

/**
 * 
 * 
 */
const AddTask: React.FC = () => {
  const dispatch = useAppDispatch();
  const categories = useAppSelector((state) => state.categories.categories);
  
  // 
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [categoryId, setCategoryId] = useState<string>('');
  const [priority, setPriority] = useState<Priority>('medium');
  const [open, setOpen] = useState(false);
  
  // 
  const [subtasks, setSubtasks] = useState<Subtask[]>([]);

  /**
   * 
   * @param e - 
   */
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (title.trim()) {
      dispatch(addTask({
        title: title.trim(),
        description: description.trim(),
        categoryId: categoryId || null,
        priority,
        completed: false,
        subtasks,
      }));
      resetForm();
    }
  };

  /**
   * 
   */
  const handleCancel = () => {
    resetForm();
  };

  /**
   * 
   */
  const resetForm = () => {
    setTitle('');
    setDescription('');
    setCategoryId('');
    setPriority('medium');
    setSubtasks([]);
    setOpen(false);
  };

  return (
    <>
      <CustomButton
        variant="contained"
        color="primary"
        size="large"
        startIcon={<AddCircleIcon />}
        onClick={() => setOpen(true)}
        sx={{ mb: 2 }}
      >
        
      </CustomButton>
      
      <Dialog open={open} onClose={handleCancel} maxWidth="sm" fullWidth>
        <DialogTitle sx={COMMON_STYLES.dialog.title}>
          
        </DialogTitle>
        
        <DialogContent sx={{ p: 4, ...COMMON_STYLES.dialog.content }}>
          <Box component="form" onSubmit={handleSubmit} sx={{ display: 'flex', flexDirection: 'column', gap: 4, mt: 2 }}>
            {/*  */}
            <CustomTextField
              label=""
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              required
              fullWidth
            />
            
            {/*  */}
            <CustomTextField
              label=""
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              multiline
              rows={4}
              fullWidth
            />
            
            {/*  */}
            <CustomFormControl>
              <InputLabel></InputLabel>
              <Select
                value={categoryId}
                label=""
                onChange={(e) => setCategoryId(e.target.value)}
              >
                <MenuItem value="">
                  <em></em>
                </MenuItem>
                {categories.map((category) => (
                  <MenuItem key={category.id} value={category.id}>
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                      <Box
                        sx={{
                          width: 16,
                          height: 16,
                          backgroundColor: category.color,
                          borderRadius: '50%',
                        }}
                      />
                      {category.name}
                    </Box>
                  </MenuItem>
                ))}
              </Select>
            </CustomFormControl>
            
            {/*  */}
            <CustomFormControl>
              <InputLabel></InputLabel>
              <Select
                value={priority}
                label=""
                onChange={(e) => setPriority(e.target.value as Priority)}
              >
                <MenuItem value="urgent"></MenuItem>
                <MenuItem value="high"></MenuItem>
                <MenuItem value="medium"></MenuItem>
                <MenuItem value="low"></MenuItem>
              </Select>
            </CustomFormControl>

            {/*  */}
            <SubtaskManager
              subtasks={subtasks}
              onSubtasksChange={setSubtasks}
            />
          </Box>
        </DialogContent>
        
        <DialogActions sx={{ px: 4, py: 2, ...COMMON_STYLES.dialog.actions }}>
          <CustomButton 
            onClick={handleCancel} 
            startIcon={<Cancel />}
            variant="outlined"
            color="primary"
            size="medium"
          >
            
          </CustomButton>
          <CustomButton 
            onClick={handleSubmit} 
            variant="contained" 
            disabled={!title.trim()} 
            startIcon={<Add />}
            color="primary"
            size="medium"
          >
            
          </CustomButton>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default AddTask; 