import React, { useState } from 'react';
import {
  Box,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
} from '@mui/material';
import { Save, Cancel } from '@mui/icons-material';
import { Category } from '../types/category';
import CustomButton from '../custom_props/CustomButton';
import CustomTextField from '../custom_props/CustomTextField';
import ColorPicker from './ColorPicker';
import { COMMON_STYLES } from '../custom_props/styles';

interface CategoryEditDialogProps {
  category: Category;
  open: boolean;
  onClose: () => void;
  onSave: (updatedCategory: Category) => void;
}

/**
 * 
 * 
 * @param category - 
 * @param open - 
 * @param onClose - 
 * @param onSave - 
 */
const CategoryEditDialog: React.FC<CategoryEditDialogProps> = ({ 
  category, 
  open, 
  onClose, 
  onSave 
}) => {
  const [editName, setEditName] = useState(category.name);
  const [editColor, setEditColor] = useState(category.color);

  /**
   * 
   */
  const handleSave = () => {
    if (editName.trim()) {
      onSave({
        ...category,
        name: editName.trim(),
        color: editColor,
      });
      onClose();
    }
  };

  /**
   * 
   */
  const handleCancel = () => {
    setEditName(category.name);
    setEditColor(category.color);
    onClose();
  };

  return (
    <Dialog open={open} onClose={handleCancel} maxWidth="sm" fullWidth>
      <DialogTitle sx={COMMON_STYLES.dialog.title}>
        
      </DialogTitle>
      
      <DialogContent sx={{ p: 4, ...COMMON_STYLES.dialog.content }}>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 4, mt: 2 }}>
          {/*  */}
          <CustomTextField
            label=""
            value={editName}
            onChange={(e) => setEditName(e.target.value)}
            fullWidth
            required
          />
          
          {/*  */}
          <ColorPicker
            color={editColor}
            onChange={setEditColor}
          />
        </Box>
      </DialogContent>
      
      <DialogActions sx={{ px: 4, py: 2, ...COMMON_STYLES.dialog.actions }}>
        <CustomButton 
          onClick={handleCancel} 
          startIcon={<Cancel />}
          variant="outlined"
          color="primary"
          size="medium"
        >
          
        </CustomButton>
        <CustomButton 
          onClick={handleSave} 
          variant="contained" 
          disabled={!editName.trim()} 
          startIcon={<Save />}
          color="primary"
          size="medium"
        >
          
        </CustomButton>
      </DialogActions>
    </Dialog>
  );
};

export default CategoryEditDialog; 