import React, { useState } from 'react';
import {
  Box,
  InputLabel,
  Select,
  MenuItem,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton,
  Checkbox,
} from '@mui/material';
import { Add, Delete, Edit, Save, Cancel } from '@mui/icons-material';
import { useAppDispatch, useAppSelector } from '../hooks/redux';
import { updateTask } from '../store/taskSlice';
import { Task, Priority, Subtask } from '../types/task';
import CustomButton from '../custom_props/CustomButton';
import CustomTextField from '../custom_props/CustomTextField';
import CustomFormControl from '../custom_props/CustomFormControl';
import CustomTypography from '../custom_props/CustomTypography';
import { COMMON_STYLES } from '../custom_props/styles';

interface TaskEditDialogProps {
  task: Task;
  open: boolean;
  onClose: () => void;
}

/**
 * 
 * 
 * @param task - 
 * @param open - 
 * @param onClose - 
 */
const TaskEditDialog: React.FC<TaskEditDialogProps> = ({ task, open, onClose }) => {
  const dispatch = useAppDispatch();
  const categories = useAppSelector((state) => state.categories.categories);
  
  // 
  const [editTitle, setEditTitle] = useState(task.title);
  const [editDescription, setEditDescription] = useState(task.description);
  const [editCategoryId, setEditCategoryId] = useState(task.categoryId || '');
  const [editPriority, setEditPriority] = useState<Priority>(task.priority);
  
  // 
  const [editSubtasks, setEditSubtasks] = useState<Subtask[]>(task.subtasks || []);
  const [newSubtaskTitle, setNewSubtaskTitle] = useState('');
  const [editingSubtaskId, setEditingSubtaskId] = useState<string | null>(null);
  const [editingSubtaskTitle, setEditingSubtaskTitle] = useState('');

  /**
   * 
   */
  const handleSave = () => {
    if (editTitle.trim()) {
      dispatch(updateTask({
        id: task.id,
        updates: {
          title: editTitle.trim(),
          description: editDescription.trim(),
          categoryId: editCategoryId || null,
          priority: editPriority,
          subtasks: editSubtasks,
        },
      }));
      onClose();
      resetEditForm();
    }
  };

  /**
   * 
   */
  const handleCancel = () => {
    setEditTitle(task.title);
    setEditDescription(task.description);
    setEditCategoryId(task.categoryId || '');
    setEditPriority(task.priority);
    setEditSubtasks(task.subtasks || []);
    resetEditForm();
    onClose();
  };

  /**
   * 
   */
  const resetEditForm = () => {
    setNewSubtaskTitle('');
    setEditingSubtaskId(null);
    setEditingSubtaskTitle('');
  };

  /**
   * 
   */
  const handleAddSubtask = () => {
    if (newSubtaskTitle.trim()) {
      const newSubtask: Subtask = {
        id: Date.now().toString(),
        title: newSubtaskTitle.trim(),
        completed: false,
      };
      setEditSubtasks([...editSubtasks, newSubtask]);
      setNewSubtaskTitle('');
    }
  };

  /**
   * 
   * @param subtaskId - ID
   */
  const handleDeleteSubtask = (subtaskId: string) => {
    setEditSubtasks(editSubtasks.filter(st => st.id !== subtaskId));
  };

  /**
   * 
   * @param subtaskId - ID
   */
  const handleToggleEditSubtaskComplete = (subtaskId: string) => {
    setEditSubtasks(editSubtasks.map(st => 
      st.id === subtaskId ? { ...st, completed: !st.completed } : st
    ));
  };

  /**
   * 
   * @param subtask - 
   */
  const handleStartEditSubtask = (subtask: Subtask) => {
    setEditingSubtaskId(subtask.id);
    setEditingSubtaskTitle(subtask.title);
  };

  /**
   * 
   */
  const handleSaveEditSubtask = () => {
    if (editingSubtaskTitle.trim() && editingSubtaskId) {
      setEditSubtasks(editSubtasks.map(st => 
        st.id === editingSubtaskId 
          ? { ...st, title: editingSubtaskTitle.trim() }
          : st
      ));
      setEditingSubtaskId(null);
      setEditingSubtaskTitle('');
    }
  };

  /**
   * 
   */
  const handleCancelEditSubtask = () => {
    setEditingSubtaskId(null);
    setEditingSubtaskTitle('');
  };

  return (
    <Dialog open={open} onClose={handleCancel} maxWidth="sm" fullWidth>
      <DialogTitle sx={COMMON_STYLES.dialog.title}>
        
      </DialogTitle>
      
      <DialogContent sx={{ p: 4, ...COMMON_STYLES.dialog.content }}>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 4, mt: 2 }}>
          {/*  */}
          <CustomTextField
            label=""
            value={editTitle}
            onChange={(e) => setEditTitle(e.target.value)}
            fullWidth
            required
          />
          
          {/*  */}
          <CustomTextField
            label=""
            value={editDescription}
            onChange={(e) => setEditDescription(e.target.value)}
            multiline
            rows={3}
            fullWidth
          />
          
          {/*  */}
          <CustomFormControl>
            <InputLabel></InputLabel>
            <Select
              value={editPriority}
              label=""
              onChange={(e) => setEditPriority(e.target.value as Priority)}
            >
              <MenuItem value="urgent"></MenuItem>
              <MenuItem value="high"></MenuItem>
              <MenuItem value="medium"></MenuItem>
              <MenuItem value="low"></MenuItem>
            </Select>
          </CustomFormControl>
          
          {/*  */}
          <CustomFormControl>
            <InputLabel></InputLabel>
            <Select
              value={editCategoryId}
              label=""
              onChange={(e) => setEditCategoryId(e.target.value)}
            >
              <MenuItem value="">
                <em></em>
              </MenuItem>
              {categories.map((category) => (
                <MenuItem key={category.id} value={category.id}>
                  <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <Box
                      sx={{
                        width: 16,
                        height: 16,
                        backgroundColor: category.color,
                        borderRadius: '50%',
                      }}
                    />
                    {category.name}
                  </Box>
                </MenuItem>
              ))}
            </Select>
          </CustomFormControl>

          {/*  */}
          <Box sx={{ mt: 2 }}>
            <CustomTypography variant="h6" size="large" color="primary" sx={{ mb: 2, fontWeight: 700 }}>
              
            </CustomTypography>
            
            {/*  */}
            <Box sx={{ display: 'flex', gap: 1, alignItems: 'center', mb: 2 }}>
              <CustomTextField
                label=""
                value={newSubtaskTitle}
                onChange={(e) => setNewSubtaskTitle(e.target.value)}
                size="small"
                fullWidth
              />
              <IconButton
                onClick={handleAddSubtask}
                disabled={!newSubtaskTitle.trim()}
                size="small"
                sx={{
                  borderRadius: 2,
                  background: newSubtaskTitle.trim()
                    ? 'linear-gradient(90deg, #1976d2 60%, #1565c0 100%)'
                    : '#e5e7eb',
                  color: newSubtaskTitle.trim() ? '#fff' : '#9ca3af',
                  '&:hover': {
                    background: newSubtaskTitle.trim()
                      ? 'linear-gradient(90deg, #1565c0 60%, #1976d2 100%)'
                      : '#e5e7eb',
                  },
                  '&:disabled': {
                    background: '#e5e7eb',
                    color: '#9ca3af',
                    transform: 'none',
                  },
                }}
              >
                <Add fontSize="small" />
              </IconButton>
            </Box>

            {/*  */}
            {editSubtasks.length > 0 && (
              <Box sx={{ mt: 2 }}>
                {editSubtasks.map((subtask) => (
                  <Box key={subtask.id} sx={{ display: 'flex', alignItems: 'center', mb: 1, p: 1, backgroundColor: '#f8fafc', borderRadius: 1 }}>
                    <Checkbox
                      checked={subtask.completed}
                      onChange={() => handleToggleEditSubtaskComplete(subtask.id)}
                      size="small"
                      sx={{ mr: 1 }}
                    />
                    {editingSubtaskId === subtask.id ? (
                      <Box sx={{ display: 'flex', gap: 1, alignItems: 'center', flexGrow: 1 }}>
                        <CustomTextField
                          value={editingSubtaskTitle}
                          onChange={(e) => setEditingSubtaskTitle(e.target.value)}
                          size="small"
                          fullWidth
                        />
                        <IconButton
                          onClick={handleSaveEditSubtask}
                          size="small"
                          sx={{
                            borderRadius: 50,
                            '&:hover': {
                              background: 'rgba(76, 175, 80, 0.08)',
                              color: '#4caf50',
                            },
                          }}
                        >
                          <Save fontSize="small" />
                        </IconButton>
                        <IconButton
                          onClick={handleCancelEditSubtask}
                          size="small"
                          sx={{
                            borderRadius: 50,
                            '&:hover': {
                              background: 'rgba(158, 158, 158, 0.08)',
                              color: '#9e9e9e',
                            },
                          }}
                        >
                          <Cancel fontSize="small" />
                        </IconButton>
                      </Box>
                    ) : (
                      <>
                        <CustomTypography
                          color={subtask.completed ? 'muted' : 'text'}
                          sx={{
                            textDecoration: subtask.completed ? 'line-through' : 'none',
                            flexGrow: 1,
                          }}
                        >
                          {subtask.title}
                        </CustomTypography>
                        <IconButton
                          onClick={() => handleStartEditSubtask(subtask)}
                          size="small"
                          sx={{
                            borderRadius: 50,
                            mr: 1,
                            '&:hover': {
                              background: 'rgba(25, 118, 210, 0.08)',
                              color: '#1976d2',
                            },
                          }}
                        >
                          <Edit fontSize="small" />
                        </IconButton>
                        <IconButton
                          onClick={() => handleDeleteSubtask(subtask.id)}
                          size="small"
                          color="error"
                          sx={{
                            borderRadius: 50,
                            '&:hover': {
                              background: 'rgba(244, 67, 54, 0.08)',
                              color: '#d32f2f',
                            },
                          }}
                        >
                          <Delete fontSize="small" />
                        </IconButton>
                      </>
                    )}
                  </Box>
                ))}
              </Box>
            )}
          </Box>
        </Box>
      </DialogContent>
      
      <DialogActions sx={{ px: 4, py: 2, ...COMMON_STYLES.dialog.actions }}>
        <CustomButton 
          onClick={handleCancel} 
          startIcon={<Cancel />}
          variant="outlined"
          color="primary"
          size="medium"
        >
          
        </CustomButton>
        <CustomButton 
          onClick={handleSave} 
          variant="contained" 
          disabled={!editTitle.trim()} 
          startIcon={<Save />}
          color="primary"
          size="medium"
        >
          
        </CustomButton>
      </DialogActions>
    </Dialog>
  );
};

export default TaskEditDialog; 